import {Hexagon, HexGrid, Layout,GridGenerator} from 'react-hexgrid';
import React, {Component} from 'react';

export default class App extends Component {
    render() {
        const generator = GridGenerator.getGenerator("parallelogram");
        const hexagons = generator.apply(this, [ -4, 4, -4, 4 ]);
        return (
            <HexGrid width="100%" height="100%" >
                <Layout size={{x: 3, y: 3}} flat={true} spacing={1.05} origin={{x: 0, y: 0}}>
                    {
                        hexagons.map((hex, i) => (
                            <Hexagon key={i} q={hex.q} r={hex.r} s={hex.s}/>
                        ))
                    }
                </Layout>
            </HexGrid>
        );
    }
}
